
import axios from "axios"
const uri = "https://aqueous-tor-26546.herokuapp.com/getData"

const getData = async () => {
    let data = await axios.get(uri);
    // let x = data.data.attempts;
    data.data.attempts.forEach((length) => {
        length.sort((a,b) => -a.steps + b.steps)

    })
    return data.data;
}

export default getData


 